export default {
	data() {
		return {
			shrink:[],
			tradeId:"",
		} 
	},
	onLoad:function(option){
		var _self = this
		_self.tradeId = option.id
		_self.getInfo()
	},
	methods: {
		getInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/declareNewQuery",
				data:{id:"0",mark:0,optionId:uni.getStorageSync("openId"),branch:0,trade:_self.tradeId},
				method:"GET",
				success:function(res){
					console.log(res)
					var data = res.data
					_self.shrink = res.data  
				}
			})
		},
		goto:function(id,mark,trade){
			var _self = this 
			uni.navigateTo({
				url:"../myComponyNeedDetail/myComponyNeedDetail?id="+id+"&mark="+mark+"&tradeId="+trade
			})
		},
	},
	filters:{
			getPic:function(res){
			return res.split(',')[0]
		},
		getTime:function(res){
			return res.slice(0,11)
		}
	}
}
